<template>
  <div class="hotitem" @click="songitemapp">
    <div class="sgfl">
      <h1 v-text="(hot.index + 1) < 10 ? ('0'+(hot.index + 1)):(hot.index + 1)"></h1>
      <div class="hot-sg">
        <div class="hot-name">{{ hot.hot.name }}</div>
        <div class="hot-text">
          <i></i>{{ hot.hot.ar[0].name }} - {{ hot.hot.al.name }}
        </div>
      </div>
    </div>
    <p class="newsong-play" v-if="hot.hot.id !==songId"></p>
    <div v-else class="newsong-play active" :class="{ playing: playing }">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    hot: Object,
     playing: Boolean,
       songId: [Number, String],
  },
   methods:{
    songitemapp:function () {
      this.$emit('update-song', this.hot.hot);
      // console.log(this.hot);
    }
  }
};
</script>

<style lang="less">
@keyframes playingrunning {
  from {
    transform: rotateX(5deg);
  }
  to {
    transform: rotateX(80deg);
  }
}
.hotitem {
  padding: 6rem 10rem;
  border-bottom: 1rem solid rgba(150, 145, 145, 0.168);
  display: flex;
  justify-content: space-between;
  align-items: center;
  .sgfl {
    display: flex;
    align-items: center;

    h1 {
      width: 30rem;
      font-size: 17rem;
      color: #999;
    }
    .hot-sg {
      .hot-name {
        font-size: 17rem;
        width: 250rem;
      
      }
      .hot-text {
        color: #888;
        font-size: 12rem;
        i {
          display: inline-block;
          width: 12rem;
          height: 8rem;
          background-image: url(../img/index_icon_2x.png);
          margin-right: 4rem;
          background-size: 166rem 97rem;
        }
      }
    }
  }
  // p {
  //   // float: right;
  //   width: 22rem;
  //   height: 22rem;
  //   background-position: -24rem 0;
  //   background-size: 166rem 97rem;
  //   background-image: url(../img/index_icon_2x.png);
  // }
    .newsong-play {
    width: 22rem;
    height: 22rem;
    background-position: -24rem 0;
    background-size: 166rem 97rem;
    background-image: url(../img/index_icon_2x.png);

    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;
      span {
        width: 11%;
        height: 80%;
        border-radius: 5rem;
        background: linear-gradient(to bottom, #1ee95d, #ce1417 70%);
        animation: playingrunning 0.8s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;
        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }
      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
}
</style>