<template>
  <div class="hot">
    <div class="hotcard">
      <div class="hoticon">
        <p></p>
        <span>更新日期：{{ month }}月{{ date }}日</span>
      </div>
    </div>
    <div class="loading" v-show="loading">
      <img src="../img/loading.gif" alt="">
    </div>
    <div class="hotitems">
      <HotItem
        v-for="(hot, index) in hotsong"
        :key="hot.id"
        :hot="{ hot, index }"
           @update-song="songitemapp($event)"
            :playing="playing"
             :songId="songId"
      />
    </div>
  </div>
</template>

<script>
import HotItem from "@/components/HotItem.vue";
export default {
  data: function () {
    return {
      hotsong: [],
      month: 0,
      date: 0,
      loading:false
    };
  },
  props:{
     playing:Boolean,
       songId: [Number, String],
  },
  components: {
    HotItem,
  },
  created() {
    this.loading=true;
    this.axios
      .get("https://apis.netstart.cn/music/playlist/detail?id=3778678", {
        params: { limit: 20 },
      })
      .then((res) => {
        this.hotsong = res.data.playlist.tracks.slice(0, 20);
        this.month = new Date().getMonth() + 1;
        this.date = new Date().getDate();
        // console.log(res, this.date);
         this.loading=false;
      });
  },
  
  methods:{
songitemapp:function (event) {
  this.$emit('update-song',event);
  this.$emit('update-songhome', this.hotsong)
// console.log(event,this.newsongs);
}
  },



};
</script>

